import React, {useState} from 'react';
import s from '../Dialogs/Dialogs.module.css'
import DialogsList from "./DialogsList";
import DialogForm from "./DialogForm";
import MyButton from "../../UI/button/MyButton";
import MyModal from "../../UI/myModal/MyModal";

const Dialogs = () => {

    const [messages, setMessages] = useState([
        {id:1, dialog: 'djskdjsfkjf'},
        {id:2, dialog: 'djskdfrgeeetgthtjt'},
        {id:3, dialog: 'yhdjskdjsfkjhtyhtyhtf'},
        {id:4, dialog: 'tyhtyhtdjskdjsfkjf'}
    ])
    const [myModal, setMyModal] = useState(false)
    const createMessage = (newMessage) => {
        setMessages([...messages, newMessage])
        setMyModal(false)
    }

    const removeMessage = (message) => {
        setMessages(messages.filter(m => m.id !== message.id))
    }

    return (
        <div className ={s.dialogs}>

           <div>
               <h3 style = {{marginTop: '15px'}}> My Dialogs </h3>
               <MyButton style = {{marginTop: '25px ', float: 'right'}}
                         onClick = { () => setMyModal(true)} > create message </MyButton>
               <div>
                   <DialogsList messages ={messages}  remove ={removeMessage}/>
               </div>

               <MyModal visible ={myModal} setVisible = {setMyModal}>
                   <DialogForm  create ={ createMessage }/>

               </MyModal>
           </div>

        </div>
    )
}

export default Dialogs;





