import React from 'react';
import s from './Dialogs.module.css'
import MyButton from "../../UI/button/MyButton";



const DialogItem = (props) => {

    return (


        <div className ={s.item}>

            <div className ={s.imgItem}>
                <img src='https://www.kino-teatr.ru/acter/foto/star/22467.jpg' />
                <div> {props.name}</div>
            </div>

           <div> {props.number}. {props.message.dialog}</div>
            <div className ={s.btnItem}>
                <MyButton className = 'btn btn-primary' onClick= {() => props.remove(props.message)}> remove </MyButton>
                <MyButton className = 'btn btn-primary' > open </MyButton>
            </div>
        </div>



    )
};

export default DialogItem;