import React, {useState} from 'react';
import MyInput from "../../UI/input/MyInput";
import MyButton from "../../UI/button/MyButton";

const DialogForm = (create) => {


    const [message, setMessage] = useState({dialog: ''})

    function addNewMessage  (e) {
        e.preventDefault()
        const newMessage = {
            ...message, id: Date.now()
        }
        create (newMessage)
        setMessage({dialog: ''})

    }
    return (
        <div>
            <form>
                <MyInput placeholder = 'Enter you message' type = 'text' value = {message.dialog}
                         onChange ={e=> setMessage({...message, dialog: e.target.value})} />

            </form>
            <MyButton onClick = {addNewMessage} className = 'btn btn-primary'> add NewMessage</MyButton>
        </div>
    );
};

export default DialogForm;
